<template>
	<view class="page">

		<view class="input-t">
			<text>体温</text>
			<text class="input-quick"></text>
		</view>
		<view class="input-body">
			<textarea placeholder="请输入体温..." v-model="sendDate.animalHeat" class="input-textare" />
		</view>
		
		<view class="input-t">
			<text>离校返校情况</text>
			<text class="input-quick"></text>
		</view>
		<view class="input-body">
			<textarea placeholder="请输入离校返校情况..." v-model="sendDate.leaveReturnSchoolCondition" class="input-textare" />
		</view>
		
		<view class="input-t">
			<text>健康情况</text>
			<text class="input-quick"></text>
		</view>
		<view class="input-body">
			<textarea placeholder="请输入健康情况..." v-model="sendDate.healthCondition" class="input-textare" />
		</view>

		<view class="covers-title">
			<text>健康码图片</text>
		</view>
		<view class="covers-body covers-uploader oa-uploader">
			<view class="uni-uploader">
				<view class="uni-uploader-head">
					<view class="uni-uploader-title">点击预览图片</view>
					<view class="uni-uploader-info">{{ imageList.length }}/1</view>
				</view>
				<view class="uni-uploader-body">
					<view class="uni-uploader__files">
						<block v-for="(image, index) in imageList" :key="index">
							<view class="uni-uploader__file" style="position: relative;">
								<oa-image class="uni-uploader__img" :src="image"></oa-image>
								<view class="close-view" v-if="type === 1" @tap="close(index)" :class="'bg-' + themeColor.name">x</view>
							</view>
						</block>
						<view class="uni-uploader__input-box" v-if="imageList.length < 1">
							<view class="uni-uploader__input" @tap="uploadImage"></view>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view class="covers-title">
			<text>行程码图片</text>
		</view>
		<view class="covers-body covers-uploader oa-uploader">
			<view class="uni-uploader">
				<view class="uni-uploader-head">
					<view class="uni-uploader-title">点击预览图片</view>
					<view class="uni-uploader-info">{{ imageList2.length }}/1</view>
				</view>
				<view class="uni-uploader-body">
					<view class="uni-uploader__files">
						<block v-for="(image, index) in imageList2" :key="index">
							<view class="uni-uploader__file" style="position: relative;">
								<oa-image class="uni-uploader__img" :src="image"></oa-image>
								<view class="close-view" v-if="type === 1" @tap="close2(index)" :class="'bg-' + themeColor.name">x</view>
							</view>
						</block>
						<view class="uni-uploader__input-box" v-if="imageList2.length < 1">
							<view class="uni-uploader__input" @tap="uploadImage2"></view>
						</view>
					</view>
				</view>
			</view>
		</view>
		<button v-if="type === 1" class="confirm-btn" :class="'bg-' + themeColor.name" :disabled="btnLoading" :loading="btnLoading"
			@tap="nextStep">
			下一步
		</button>

	</view>
</template>

<script>
	import {
		uploadImage
	} from '@/api/userInfo';
	import {
		check,
		attendCheckType,
		getAttendClasses
	} from '@/api/basic';
	import nativeUtil from "@/utils/native-nfc-util.js"
	import requests from '@/utils/requests.js'
	export default {
		data() {
			return {
				type: 1,
				sendDate: {
					animalHeat: '', // 体温
					leaveReturnSchoolCondition: '',
					healthCondition: ''
				},
				place: {},
				imgLink: '', // 健康码
				imgLink2: '', // 行程码
				checkType: [{
					"key": "0",
					"value": "签到"
				}, {
					"key": "1",
					"value": "签退"
				}],
				classesType: [{
					"key": "1",
					"value": "白班"
				}],
				imageList: [],
				imageList2: [],
				btnLoading: false
			};
		},
		onLoad(e) {
			console.log(e)
			this.type = Number(e.type)
			if(this.type === 2){
				this.sendDate = JSON.parse(e.obj)	
				this.imageList.push(this.sendDate.healthCode)
				this.imageList2.push(this.sendDate.tourCode)
			}
			
		},
		onShow() {
			uni.setNavigationBarColor({
		 	frontColor: '#ffffff',
				backgroundColor: this.themeColor.color,
				animation: {
					duration: 400,
					timingFunc: 'easeIn'
				}
			})
		},

		methods: {
			// 监听反馈类型事件
			handleClassesChange(e) {
				this.sendDate.classes_id = e.detail.value;
			},
			handleStateChange(e) {
				this.sendDate.type = e.detail.value;
			},

			// 健康码打开相册选图
			uploadImage() {
				const _this = this;
				uni.chooseImage({
					count: 1, // 最多一次可以选择的图片张数
					sizeType: ['compressed'],
					sourceType: ['camera','album'], // album 从相册选图，camera 使用相机
					success: function(res) {
						let tempFilePath = res.tempFilePaths[0];
						_this.imageList.push(tempFilePath)
						uni.uploadFile({
							url: requests.getBaseUrl(),
							filePath: tempFilePath,
							name: 'file',
							fileType: 'image',
							header: {
								'Authorization': 'Bearer ' + uni.getStorageSync('token'),
								'client-type': 'MA', //客户端类型小程序
							},
							formData: {
								filePath: '/uploadFileImage/',
								fileType: 'image',
							},
							success: function(resp) {
								let link = (JSON.parse(resp.data).link)
								
								if(link){
									_this.imgLink = link
								}else{
									_this.imageList = []
									uni.showToast({
										title: '上传失败，请重试'
									})
								}
								console.log('link:', link);
							}
						});

					}
				});
			},

			// 行程码打开相册选图
			uploadImage2() {
				const _this = this;
				uni.chooseImage({
					count: 1, // 最多一次可以选择的图片张数
					sizeType: ['compressed'],
					sourceType: ['camera','album'], // album 从相册选图，camera 使用相机
					success: function(res) {
						let tempFilePath = res.tempFilePaths[0];
						_this.imageList2.push(tempFilePath)
						uni.uploadFile({
							url: requests.getBaseUrl(),
							filePath: tempFilePath,
							name: 'file',
							fileType: 'image',
							header: {
								'Authorization': 'Bearer ' + uni.getStorageSync('token'),
								'client-type': 'MA', //客户端类型小程序
							},
							formData: {
								filePath: '/uploadFileImage/',
								fileType: 'image',
							},
							success: function(resp) {
								let link = (JSON.parse(resp.data).link)
								if(link){
									_this.imgLink2 = link
								}else{
									_this.imageList2 = []
									uni.showToast({
										title: '上传失败，请重试'
									})
								}
								
								console.log('link:', link);
							}
						});

					}
				});
			},
			// 依次上传图片
			handleUploadFile(data) {
				console.log('data===>', data)
				this.imageList.push(data[0])
				this.$mHelper.toast('上传成功');
			},

			// 依次上传图片
			handleUploadFile2(data) {
				console.log('data===>', data)
				this.imageList2.push(data[0])
				this.$mHelper.toast('上传成功');
			},
			// 删除已选中图片
			close(e) {
				this.imageList.splice(e, 1);
				this.imgLink = ''
			},
			// 删除已选中图片
			close2(e) {
				this.imageList2.splice(e, 1);
				this.imgLink2 = ''
			},
			// 发送数据
			async nextStep() {
				let that = this
				if (that.sendDate.animalHeat === '' || that.sendDate.animalHeat === null) {
					uni.showToast({
						title: '请输入体温',
						icon: 'none'
					})
					return
				}
				if (isNaN(that.sendDate.animalHeat)) {
					uni.showToast({
						title: '体温输入有误',
						icon: 'none'
					})
					return
				}
				if (that.sendDate.leaveReturnSchoolCondition === '' || that.sendDate.leaveReturnSchoolCondition === null) {
					uni.showToast({
						title: '请输入离校返校情况',
						icon: 'none'
					})
					return
				}
				if (that.sendDate.healthCondition === '' || that.sendDate.healthCondition === null) {
					uni.showToast({
						title: '请输入健康状况',
						icon: 'none'
					})
					return
				}
				if (that.imgLink === '' || that.imgLink === null) {
					uni.showToast({
						title: '请上传健康码',
						icon: 'none'
					})
					return
				}
				if (that.imgLink2 === '' || that.imgLink === null) {
					uni.showToast({
						title: '请上传行程码',
						icon: 'none'
					})
					return
				}
				let userInfo = uni.getStorageSync('userInfo');
				let obj = Object.assign({
					animalHeat: that.sendDate.animalHeat,
					leaveReturnSchoolCondition: that.sendDate.leaveReturnSchoolCondition,
					healthCondition: that.sendDate.healthCondition,
					healthCode: that.imgLink,
					tourCode: that.imgLink2,
					userId: userInfo.userId,
					userName: userInfo.userName,
					phoneNumber: userInfo.phonenumber,
				})
				
				console.log(obj)
				
				uni.navigateTo({
					url: './checkin?form=' + JSON.stringify(obj)
				})
			}
		}
	};
</script>

<style lang="scss">
	page {
		background-color: $page-color-base;
	}

	.cu-form-group {
		border-style: none;
		background-color: $page-color-base;
		padding: $spacing-base;
		width: 100%;
		display: flex;
		align-items: center;
		// min-height: 100upx;
		justify-content: space-between;
	}

	/*表单标题*/
	.input-t {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: $spacing-base;
		margin-top: $spacing-base;
		font-size: $font-base;

	}

	.tit {
		width: 80%;
		font-size: $font-sm + 2upx;
		color: $font-color-base;
	}

	.input-quick {
		position: relative;
		padding-right: 40upx;

		.iconfont {
			font-size: $font-sm;
		}
	}

	.input-body {
		background: $color-white;
		padding: $spacing-sm $spacing-base;
	
	.gender-item {
			margin-right: 20upx;

			.gender-item-text {
				padding-left: 10upx;
			}

		}
	}

	.input-textare {
		height: 200upx;
		font-size: 34upx;
		line-height: 50upx;
		width: 100%;
		box-sizing: border-box;
		padding: 20upx 30upx 0;
	}

	.covers-title {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: $spacing-base;
		margin-top: $spacing-base;
		font-size: $font-base;

	}

	.covers-body {
		background: $color-white;
		padding: $spacing-sm $spacing-base;

		.gender-item {
			margin-right: 20upx;

			.gender-item-text {
				padding-left: 10upx;
			}

			radio .wx-radio-input.wx-radio-input-checked {
				background: $uni-color-primary;
				border-color: $uni-color-primary;
			}
		}
	}

	.covers-uploader {
		padding: 22upx 20upx;
	}
</style>
